body {
  margin: 0;
  padding: 0;
  /* font-family: sans-serif; */
}

html {
  overflow: scroll;
  overflow-x: hidden;
}
html, body {
  max-width: 100%;
  overflow-x: hidden;
  font-family: "Avenir";
}
::-webkit-scrollbar {
  width: 0px;  /* remove scrollbar space */
  background: transparent;  /* optional: just make scrollbar invisible */
}

.App {
  /*text-align: center;*/
  position: fixed;
}

.tick-line, .axis-baseline {
  stroke: #AAAAAA;
  stroke-width: 1px;
}

.tick-line {
  stroke-dasharray: 4;
}

.axis-label, .ordinal-labels {
  font-size: 12px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 200;
  font-style: normal;
  color: #cccccc;
  -webkit-font-smoothing: antialiased;
}

.tooltip-content {
  background: white;
  border: 1px solid black;
  color: black;
  padding: 10px;
  z-index: 999999;
  min-width: 120px;
}

.tooltip-content:before {
  background: inherit;
  content: "";
  padding: 0px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  width: 15px;
  height: 15px;
  position: absolute;
  z-index: 99;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

#splashScreen img {
  max-height: 30px;
}

#splashScreen button.cta {
  max-width: 230px;
  margin-left: auto;
  margin-right: auto;
}

#splashScreen .info {
  font-style: italic;
  font-size: 11px;
}
#Stepper {
  font-size: 16px;
  line-height: 2.15;
  font-family: "Avenir";
  font-weight: 400;
  font-style: normal;
  color: #333;
  -webkit-font-smoothing: antialiased;

}

#Stepper.step-grid {
  display: block;
  margin-bottom: 32px;
  padding-top: 16px;
}

#Stepper.step-grid .step-item {
  display: inline-block;
  width: 25%;
  text-align: center;
  color: rgb(203, 203, 203);
  vertical-align: top;
}

#Stepper.step-grid .step-item .step-name {
  font-size: 12px;
}

#Stepper.step-grid .step-item .step-count {
  border: 1px solid rgb(203, 203, 203);
  border-radius: 50%;
  width: 24px;
  height: 24px;
  display: flex;
  margin-left: auto;
  margin-right: auto;
}

#Stepper.step-grid .step-item .step-count:after {
  position: absolute;
  width: 16%;
  height: 1px;
  background: rgb(187, 187, 187);
  content: '';
  margin-left: 2%;
  margin-top: 12px;
}

#Stepper.step-grid .step-item:last-child .step-count:after {
  width: 0;
  height: 0;
}

#Stepper.step-grid .step-item.active .step-count {
  background: rgb(45, 204, 151);
  border: 1px solid rgb(45, 204, 151);
}

#Stepper.step-grid .step-item .step-count span {
  color: rgb(203, 203, 203);
  margin: auto;
  margin-top: -5px;
}

#Stepper.step-grid .step-item.active .step-count span {
  /* font-weight: 700; */
  color: white;
}

#Stepper.step-grid .step-item.active .step-name {
  color: rgb(51, 51, 51);
  font-weight: 700;
}

.StepButtons {
  position: fixed;
  bottom: 0;
  width: 100%;
  box-shadow: 0px 0px 3px 0px rgba(0,0,0,0.25);
  z-index: 100;
  background: white;
}

.StepButtons Button.newCta {
  margin-left: 24px;
  margin-right: 24px;
  margin-top: 12px;
  margin-bottom: 12px;
  display: inline-block;
}

.StepButtons Button.newCta.next {
  float: right;
}
.loadingIndicator {
  width: 200px;
  height: 200px;
  text-align: center;
  position: absolute;
  left: calc(50vw - 100px);
  top: calc(50vh - 100px);
}

